<div class="MainFrame"><div class="name"><span>Ваше имя: {{profileName}}</span></div><div class="item"><span>Заказано:</span></div><div class="items"><div v-for="(item, index) in arrayObj.orderList" :key="index"><div class="order"><div class="name"><span>{{item.name}}</span></div><div class="count"><button class="btt" @click="countEdit(item.name, -1)">-</button><span>{{item.count}}</span><button class="btt" @click="countEdit(item.name, 1)">+</button></div><div class="price"><span>{{item.price}} руб.</span></div><div class="del"><button class="btt" @click="delItem(item.name)">Удалить</button></div></div></div></div><div class="number"><span>Номер телефона:</span><input class="inputStyle" v-model="this.mobilenumber"></div><div class="address"><span>Адрес:</span><input class="inputStyle" v-model="this.adress"></div><div class="totalPrice"><span>Общая сумма заказа: {{totalPrice}} руб.</span></div><div class="info de"><span>Оплата наличными при получении заказа!</span></div><div class="info2 de"><span>Особенности доставки уточняйте у оператора</span></div><div class="mas de"><span class="spanERR" v-show="validation.dataErr == true">Ошибка оформления</span><span class="spanOK" v-show="validation.dataOk == true">Заказ успешно оформлен</span></div><div class="de"><button class="bt" @click="postOrder()">Оформить заказ</button></div></div>