<div class="MainFrame"><div class="authStyle" v-show="loginIN == true"><div class="MainFrameAuth"><div class="AuthBack" v-show="b"><div class="titleAuth"><button class="btnAuthBack" @click="loginIN = !loginIN">✖</button></div><div class="spanTitle"><span>Авторизация</span></div><div class="inputAuth"><span>Логин</span><input class="inputStyle" v-model="auth.login" :class="{inpStyleERR: validation.dataErr}"></div><div class="inputAuth"><span>Пароль</span><input class="inputStyle" type="password" v-model="auth.password" :class="{inpStyleERR: validation.dataErr}"></div><div class="spanTitle"><span class="spanERR" v-show="validation.dataErr == true">Неправильный пароль или логин</span></div><div class="spanTitle"><button class="authBtn" @click="authMethod()">Войти</button></div><div class="regspanTitle"><button class="regBtn" @click="changeMethod()">Регистрация</button></div></div><div class="Reg AuthBack" v-show="a"><div class="titleReg titleAuth"><button class="btnRegBack btnAuthBack" @click="changeMethod()">❮</button></div><div class="spanTitle2 spanTitle"><span>Регистрация</span></div><div class="inputReg inputAuth"><span>Имя</span><input class="inputStyle" type="name" v-model="reg.name"></div><div class="inputReg inputAuth"><span>Фамилия</span><input class="inputStyle" type="surname" v-model="reg.surname"></div><div class="inputReg inputAuth"><span>Логин</span><input class="inputStyle" type="username" v-model="reg.login1"></div><div class="inputReg inputAuth"><span>Пароль</span><input class="inputStyle" type="password1" v-model="reg.password1"></div><div class="spanTitle"><span class="spanERR" v-show="validation.dataErr1 == true">Ошибка регистрации</span><span class="spanOK" v-show="validation.dataOk == true">Регистрация успешна</span></div><div class="spanTitle2 spanTitle buttonReg"><button class="RegisBtn authBtn" @click="regMethod()">Зарегистрироваться</button></div></div></div></div><div class="menuGroup1"><div><button class="btnMainLogo" @click="this.$router.push('/')">Sakura</button></div><div class="buttonGroup"><button class="btnMain" @click="this.$router.push('/admin')" v-show="profileRole[0] == 'ADMIN'">Администратор</button><div class="buttonGroup"><span class="btnMain">{{totalPrice}}</span><img class="cartStyle" src="@/assets/cart.svg" @click="this.$router.push('/cart')"></div><button class="btnSignUp" @click="loginIN = !loginIN" v-show="authed == false">Войти</button><div class="lkOutDiv"><span class="lkStyle" v-show="authed == true">{{profileName}}</span><button class="lkOut" @click="exitMethod()">Выход</button></div></div></div></div>