<div class="MainFrame"><div class="addStyle" v-show="delItems == true"><div class="MainFrameItems"><div class="delItems"><div class="titleAdd"><button class="btnAddBack" @click="delItems = !delItems">✖</button></div><div class="spanAdd"><span>Удалить позицию меню</span></div><div class="inputAdd"><span>Название</span><input class="inputStyle" type="name" v-model="item.name"></div><div class="spanTitle"><span class="spanERR1 spanERR" v-show="validation.dataErr1 == true">Данной позиции нет в меню</span><span class="spanOK1 spanOK" v-show="validation.dataOk1 == true">Успешно</span></div><div class="spanDell spanAdd"><button class="sendBtn" @click="delMethod()">Удалить</button></div></div></div></div><div class="addStyle" v-show="addItems == true"><div class="MainFrameItems"><div class="addItems"><div class="titleAdd"><button class="btnAddBack" @click="addItems = !addItems">✖</button></div><div class="spanAdd"><span>Добавить позицию меню</span></div><div class="inputAdd"><span>Название</span><input class="inputStyle" type="name" v-model="item.name"></div><div class="inputAdd"><span>Количество</span><input class="inputStyle" type="amount" v-model="item.amount"></div><div class="inputAdd"><span>Вес(граммы)</span><input class="inputStyle" type="weight" v-model="item.weight"></div><div class="selectCategor inputAdd"><span>Выберите категорию</span><input class="inputStyle" type="category" v-model="item.category"></div><div class="inputAdd"><span>Цена</span><input class="inputStyle" type="price" v-model="item.price"></div><div class="inputAdd"><span>Состав</span><input class="inputStyle" type="discription" v-model="item.discription"></div><div class="spanTitle"><span class="spanERR" v-show="validation.dataErr == true">Ошибка </span><span class="spanOK" v-show="validation.dataOk == true">Успешно</span></div><div class="spanAdd"> <button class="sendBtn" @click="addMethod()">Сохранить     </button></div></div></div></div><div class="orders"><div v-for="(item, index) in responseData" :key="index"><div class="ordere"><div class="name"><span>Имя заказчика: {{item.username}}</span></div><div class="adress"><span>Адресс: {{item.adress}}</span></div><div class="numb"><span>Номер телефона: {{item.mobileNumber}}</span></div><span>Заказ:</span><div class="items"> <div v-for="(item, index) in item.items" :key="index"><div class="iteme"><div class="name"><span>{{item.name}}</span></div><div class="count"><span>Количество: {{item.count}}</span></div><div class="price"><span>Цена: {{item.price}} руб.</span></div></div></div></div><div class="prices"><span>Общая сумма заказа: {{item.totalprice}} руб.</span></div></div></div></div><div class="btn"><div class="add"><button class="addBtn" @click="addItems = !addItems">Добавить новую позицию меню</button></div><div class="del"><button class="delBtn addBtn" @click="delItems = !delItems">Удалить позицию меню</button></div></div></div>